.service__head{
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
    color: #fff;
    font-size: 100px;
    font-weight: 700;
    padding: 0px 08vw;
}

.service__provide {
    background-color: #fff;
    padding: 100px 05vw;
}

.service__provide__head {
    color: #243E63;
    font-weight: 700;
    font-size: 32px;
    text-align: center;
}

.service__provide__mess {
    color: #243E63;
    font-size: 18px;
    text-align: center;
}

.service__diff {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background-image: url("../../assets/images/back5.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}

.service__diff__head {  
    margin: 0px 05vw;  
    color: #243E63;
    font-weight: 700;
    font-size: 32px;
    text-align: left;
    margin-bottom: 10px;
}

.service__diff__point__mess {
    margin-left: 05vw;
    margin-right: 30vw;
    color: #243E63;
    font-size: 16px;
}

@media only screen and (max-width: 800px) {
    .service__head {
        font-size: 55px;
    }

    .service__diff__head {
        font-size: 27px;
    }

    .service__diff__point__mess {
        margin-right: 05vw;
    }
}