.blog__head{
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
    color: #fff;
    font-size: 100px;
    font-weight: 700;
    padding: 0px 08vw;
}

@media only screen and (max-width: 800px) {
    .blog__head {
        font-size: 70px;
    }
}