.home__first {
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #fff;
    padding: 0px 08vw;
}

.home__first__text1 {
    font-size: 60px;
    font-weight: 900;
}

.home__first__text2 {
    font-size: 30px;
    margin-top: 10px;
}

.home__first__but {
    margin-top: 20px;
    padding: 12px 60px;
    background-color: #6415FF;
    color: #fff;
    font-size: 30px;
    cursor: pointer;
    border-radius: 20px 50px 20px;
}

.home__first__but:hover {    
    padding-top: 6px;
    padding-bottom: 18px;
}

.home__second {
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: row;
    background-color: #fff;
    padding: 120px 05vw;
}

.home__second__about {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: center;
    margin-right: 10px;
}

.home__second__about__head {
    color: #243E63;
    font-weight: 700;
    font-size: 32px;
    text-align: left;
}

.home__second__about__detail {
    color: #243E63;
    font-size: 16px;
    text-align: justify;
}

.home__second__aboutbut {
    cursor: pointer;
    margin-top: 30px;
    padding: 10px 30px;
    background-color: #6415FF;
    color: #fff;
    font-size: 20px;
    border-radius: 20px 50px 20px;
}

.home__second__aboutbut:hover {
    padding-top: 5px;
    padding-bottom: 15px;
}

.home__second__aboutimag {
    flex: 1;
}

.home__second__ab__img {
    width: 100%;
    object-fit: contain;
}

.home__slogon {
    padding: 100px 08vw;
    text-align: center;
    color: #fff;
    font-size: 35px;
}

.home__slogon__span {
    font-size: 50px;
    font-weight: 700;
}

.home__services {
    display: flex;
    width: 100%;
    height: 100vh;
    align-items: center;
    background-image: url("../../assets/images/back5.jpg");
    background-size: cover;
    background-repeat: no-repeat;
}

.home__service__content{
    flex: 1;
    padding: 0px 05vw;
}

.home__service__empty {
    flex: 1;
}

.home__service__head {    
    color: #243E63;
    font-weight: 700;
    font-size: 32px;
}

.home__service__mess {        
    color: #243E63;
    font-size: 18px;
    text-align: justify;
}

@media only screen and (max-width: 800px) {
    .home__first__text1 {
        font-size: 40px;
    }

    .home__first__text2{
        font-size: 25px;
    }

    .home__second {
        flex-direction: column;
    }
    .home__service__empty {
        display: none;
    }
}