.contact__head{
    height: calc(100vh - 80px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: start;
    color: #fff;
    font-size: 100px;
    font-weight: 700;
    padding: 0px 08vw;
}

.contact__get{
    background-color: #fff;
    padding: 100px 08vw;
}

.contact__get__head {
    font-size: 35px;
    font-weight: 700;
    color: #243E63;
    text-align: left;
    margin-bottom: 20px;
}

.contact__get__container {
    display: flex;
}

.contact__get__container__contact{
    flex: 1;
}

.contact__get__container__contact__lable {
    font-size: 20px;
    color: #243E63;
    font-weight: 600;
}

.contact__get__container__contact__value {    
    font-size: 16px;
    color: #243E63;
}

.contact__get__container__contact__valuespan {
    font-size: 18px;
    color: #243E63;
}

.contact__get__container__form{
    flex: 1;    
    background-color: #fff;
    box-shadow: 5px 8px 30px #243E63;
    border-radius: 10px;
    padding: 50px 20px;
}

.contact__get__container__form__lable { 
    font-size: 20px;
    color: #243E63;
}

.contact__get__container__form__input {
    width: 100%;
    padding: 12px 20px;
    margin: 10px 0px; 
    font-size: 20px;
    color: #243E63;
    border: 2px solid #243E63;
    box-sizing: border-box;
}

@media only screen and (max-width: 800px) {
    .contact__head {
        font-size: 55px;
    }
}